/* Enhanced Harmonics & Healing CSS - AtomixNYC Inspired */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Custom Font Import - Similar to AtomixNYC's elegant typography */
@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,300;0,400;0,500;1,300;1,400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500&display=swap');

html {
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Montserrat', 'Helvetica Neue', sans-serif;
  background-color: #000;
  color: #fff;
  overflow-x: hidden;
  line-height: 1.6;
}

/* Typography System */
h1, h2, h3, h4, h5, h6 {
  font-family: 'Cormorant Garamond', serif;
  font-weight: 300;
  letter-spacing: 0.05em;
}

/* Smooth Transitions */
* {
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

/* Custom Scrollbar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #111;
}

::-webkit-scrollbar-thumb {
  background: #333;
  border-radius: 0;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* Loading State */
.loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.loading-text {
  font-size: 0.8rem;
  letter-spacing: 0.3em;
  text-transform: uppercase;
  color: #fff;
  opacity: 0.5;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 0.3; }
  50% { opacity: 0.8; }
}

/* Navigation Enhancements */
.nav-item {
  position: relative;
  overflow: hidden;
}

.nav-item::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: -100%;
  width: 100%;
  height: 1px;
  background: white;
  transition: left 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.nav-item:hover::before {
  left: 0;
}

/* Hero Animations */
@keyframes heroFadeIn {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hero-title {
  animation: heroFadeIn 1.2s ease-out;
}

.hero-subtitle {
  animation: heroFadeIn 1.4s ease-out;
}

.hero-nav {
  animation: heroFadeIn 1.6s ease-out;
}

/* Parallax Layers */
.parallax-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 120%;
  will-change: transform;
}

.parallax-layer-1 {
  transform: translateY(calc(var(--scroll) * -0.3px));
}

.parallax-layer-2 {
  transform: translateY(calc(var(--scroll) * -0.5px));
}

.parallax-layer-3 {
  transform: translateY(calc(var(--scroll) * -0.7px));
}

/* Image Reveal Effect */
.image-reveal {
  position: relative;
  overflow: hidden;
}

.image-reveal::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  transform: scaleY(1);
  transform-origin: top;
  transition: transform 0.8s cubic-bezier(0.77, 0, 0.175, 1);
  z-index: 1;
}

.image-reveal.revealed::before {
  transform: scaleY(0);
  transform-origin: bottom;
}

/* Modal Enhancements */
.modal-content {
  opacity: 0;
  transform: translateY(60px);
  animation: modalReveal 0.8s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

@keyframes modalReveal {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Stagger Animation for Lists */
.stagger-item {
  opacity: 0;
  transform: translateY(30px);
  animation: staggerIn 0.6s ease-out forwards;
}

.stagger-item:nth-child(1) { animation-delay: 0.1s; }
.stagger-item:nth-child(2) { animation-delay: 0.2s; }
.stagger-item:nth-child(3) { animation-delay: 0.3s; }
.stagger-item:nth-child(4) { animation-delay: 0.4s; }

@keyframes staggerIn {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Subtle Noise Texture Overlay */
.noise-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.03;
  z-index: 1;
  pointer-events: none;
  background: url('data:image/svg+xml;utf8,<svg viewBox="0 0 256 256" xmlns="http://www.w3.org/2000/svg"><filter id="noise"><feTurbulence type="fractalNoise" baseFrequency="0.9" numOctaves="4"/></filter><rect width="100%" height="100%" filter="url(%23noise)" opacity="0.5"/></svg>');
}

/* Magnetic Button Effect */
.magnetic-button {
  position: relative;
  display: inline-block;
  cursor: pointer;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.magnetic-button:hover {
  transform: scale(1.05);
}

/* Split Text Animation */
.split-text {
  display: inline-block;
  overflow: hidden;
}

.split-text span {
  display: inline-block;
  transform: translateY(100%);
  animation: splitReveal 0.8s cubic-bezier(0.77, 0, 0.175, 1) forwards;
}

@keyframes splitReveal {
  to {
    transform: translateY(0);
  }
}

/* Responsive Breakpoints - AtomixNYC Style */
@media (max-width: 1440px) {
  html { font-size: 15px; }
}

@media (max-width: 1200px) {
  html { font-size: 14px; }
}

@media (max-width: 768px) {
  html { font-size: 13px; }
  
  .hero-title {
    font-size: 3rem;
    letter-spacing: 4px;
  }
  
  .modal-content {
    padding: 3rem 1.5rem;
  }
}

@media (max-width: 480px) {
  html { font-size: 12px; }
  
  .hero-title {
    font-size: 2.5rem;
    letter-spacing: 2px;
  }
  
  .hero-nav a {
    font-size: 1.2rem;
  }
}

/* Hover States for Desktop */
@media (hover: hover) {
  a, button {
    position: relative;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  a:hover, button:hover {
    transform: translateY(-2px);
  }
  
  a::after, button::after {
    content: '';
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 0;
    height: 1px;
    background: currentColor;
    transition: width 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  a:hover::after, button:hover::after {
    width: 100%;
  }
}

/* Accessibility Improvements */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}

/* Focus States */
*:focus-visible {
  outline: 2px solid #fff;
  outline-offset: 4px;
}

/* Print Styles */
@media print {
  body {
    background: white;
    color: black;
  }
  
  .no-print {
    display: none;
  }
}

/* Performance Optimizations */
.will-change-transform {
  will-change: transform;
}

.will-change-opacity {
  will-change: opacity;
}

/* GPU Acceleration for Smooth Animations */
.gpu-accelerated {
  transform: translateZ(0);
  backface-visibility: hidden;
}
